import { createRouter, createWebHashHistory } from "vue-router";
import { BasicLayout } from "../layouts";

import jobRouter from "./job";
import deptRouter from "./dept";
import empRouter from "./employee";
import contractRouter from "./contract";
import leaveRouter from "./leave";
import dataDictionaryRouter from "./dataDictionary";
import salaryRouter from "./salary";

import guard from "./guard";

const routes = [
  {
    path: "/",
    name: "home",
    component: BasicLayout,
    redirect: "/welcome",
    children: [
      {
        path: "/welcome",
        name: "about",
        component: () =>
          import(/* webpackChunkName: "welcome" */ "../views/SysWelcome.vue"),
      },
      jobRouter,
      deptRouter,
      empRouter,
      contractRouter,
      leaveRouter,
      dataDictionaryRouter,
      salaryRouter,
    ],
  },
  {
    path: "/login",
    name: "login",
    component: () =>
      import(/* webpackChunkName: "login" */ "../views/LoginView.vue"),
  },
  {
    path: "/test",
    name: "test",
    component: () =>
      import(/* webpackChunkName: "login" */ "../views/TestVue.vue"),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

// 路由守卫
guard(router);

export default router;
