import { BlankLayout } from "@/layouts";

export default {
  path: "/leave",
  name: "leave",
  component: BlankLayout,
  props: true,
  redirect: "/leave/add",
  children: [
    {
      path: "add",
      name: "leaveAdd",
      meta: { title: "请假新增" },
      component: () =>
        import(/* webpackChunkName: "leave" */ "../views/leave/LeaveAdd.vue"),
    },
    {
      path: "update/:code",
      name: "leaveUpdate",
      props: true,
      meta: { title: "请假修改" },
      component: () =>
        import(/* webpackChunkName: "leave" */ "../views/leave/LeaveAdd.vue"),
    },
    {
      path: "list",
      name: "leaveList",
      meta: { title: "请假列表" },
      component: () =>
        import(/* webpackChunkName: "leave" */ "../views/leave/LeaveList.vue"),
    },
    {
      path: "my",
      name: "leaveMy",
      props: true,
      meta: { title: "我的请假" },
      component: () =>
        import(/* webpackChunkName: "leave" */ "../views/leave/LeaveList.vue"),
    },
    {
      path: "approve",
      name: "leaveApprove",
      props: true,
      meta: { title: "请假审批" },
      component: () =>
        import(/* webpackChunkName: "leave" */ "../views/leave/LeaveList.vue"),
    },
  ],
};
