import { request } from "./request/request";

class LeaveService {
  add(data) {
    return request("/api/leave/add", {
      method: "POST",
      data,
    });
  }
  update(data) {
    return request("/api/leave/update", {
      method: "POST",
      data,
    });
  }
  delete(data) {
    return request("/api/leave/delete", {
      method: "POST",
      data,
    });
  }
  list() {
    return request("/api/leave/list", {
      method: "POST",
    });
  }
  listByEmpCode(data) {
    return request("/api/leave/listByEmpCode", {
      method: "POST",
      data,
    });
  }
  listAll() {
    return request("/api/leave/listAll", {
      method: "POST",
    });
  }
  queryByCode(data) {
    return request("/api/leave/queryByCode", {
      method: "POST",
      data,
    });
  }
  likeByCode(data) {
    return request("/api/leave/likeByCode", {
      method: "POST",
      data,
    });
  }
  queryDeptUnapprovalLeave(data) {
    return request("/api/leave/queryDeptUnapprovalLeave", {
      method: "POST",
      data,
    });
  }
  queryDeptUnapprovalLeaveNum(data) {
    return request("/api/leave/queryDeptUnapprovalLeaveNum", {
      method: "POST",
      data,
    });
  }
}

export default new LeaveService();
