import axios from "axios";
import { errorHandler, requestMiddleware, responseMiddleware } from "./utils";

const timeout = 30000;
const initOptions = {
  headers: {
    Accept: "application/json;charset=utf-8",
    "Accept-Language": "zh-CN,zh",
    "Content-Type": "application/x-www-form-urlencoded;charset=utf-8",
  },
  params: {
    timeout: timeout,
    responseData: "json", // text | stream
  },
};
const baseURL = "/";
const instance = axios.create({
  timeout,
  baseURL: baseURL,
  headers: {
    ...initOptions.headers,
  },
  ...initOptions.params,
  // validateStatus: (status) => validateStatus(status),
});

instance.interceptors.request.use(
  (config) => requestMiddleware(config),
  (error) => errorHandler(error)
);

instance.interceptors.response.use(
  (config) => responseMiddleware(config),
  (error) => errorHandler(error)
);

export function request(url, options = {}) {
  if (typeof url !== "string") {
    throw new Error("url MUST be a string");
  }
  return new Promise((resolve, reject) => {
    console.log('>>options.data', options.data)
    instance({
      url: url,
      method: (options.method || "get").toLowerCase(),
      dataType: options.dataType,
      contentType: options.contentType,
      data: options.data,
    })
      .then((response) => {
        const { data, status, statusText } = response;
        if (status === 200 || statusText === "OK") {
          resolve(data);
        } else {
          const { ERROR_CODE, ERROR_MESSAGE } = response;
          const errCode = ERROR_CODE || status;
          const errMessage = ERROR_MESSAGE || statusText;
          reject(`${errCode}--${errMessage}`);
        }
      })
      .catch((error) => console.error(error));
  });
}
