import { getUserInfo } from "@/utils/authUtil";
import { message } from "ant-design-vue";

export default (router) => {
  router.beforeEach((to, from, next) => {
    if (to.path !== "/login" && !getUserInfo()) {
      message.info("你还没有登录，请先登录！");
      next({ name: "login" });
    }

    next();
  });
};
