import { request } from "./request/request";

class DataDictionaryService {
  add(data) {
    return request("/api/dataDictionary/add", {
      method: "POST",
      data,
    });
  }
  update(data) {
    return request("/api/dataDictionary/update", {
      method: "POST",
      data,
    });
  }
  delete(data) {
    return request("/api/dataDictionary/delete", {
      method: "POST",
      data,
    });
  }
  list() {
    return request("/api/dataDictionary/list", {
      method: "POST",
    });
  }
  listAll() {
    return request("/api/dataDictionary/listAll", {
      method: "POST",
    });
  }
  queryByCode(data) {
    return request("/api/dataDictionary/queryByCode", {
      method: "POST",
      data,
    });
  }
  likeByCode(data) {
    return request("/api/dataDictionary/likeByCode", {
      method: "POST",
      data,
    });
  }
}

export default new DataDictionaryService();
