import { BlankLayout } from "@/layouts";

export default {
  path: "/dataDictionary",
  name: "dataDictionary",
  component: BlankLayout,
  props: true,
  redirect: "/dataDictionary/add",
  children: [
    {
      path: "add",
      name: "dataDictionaryAdd",
      meta: { title: "数据字典新增" },
      component: () =>
        import(/* webpackChunkName: "dataDictionary" */ "../views/dataDictionary/DataDictionaryAdd.vue"),
    },
    {
      path: "update/:code",
      name: "dataDictionaryUpdate",
      props: true,
      meta: { title: "数据字典修改" },
      component: () =>
        import(/* webpackChunkName: "dataDictionary" */ "../views/dataDictionary/DataDictionaryAdd.vue"),
    },
    {
      path: "list", 
      name: "dataDictionaryList",
      meta: { title: "数据字典列表" },
      component: () =>
        import(/* webpackChunkName: "dataDictionary" */ "../views/dataDictionary/DataDictionaryList.vue"),
    },
  ],
};
