import { BlankLayout } from "@/layouts";

export default {
  path: "/job",
  name: "job",
  component: BlankLayout,
  props: true,
  redirect: "/job/add",
  children: [
    {
      path: "add",
      name: "jobAdd",
      meta: { title: "职位新增" },
      component: () =>
        import(/* webpackChunkName: "job" */ "../views/job/JobAdd.vue"),
    },
    {
      path: "update/:code",
      name: "jobUpdate",
      props: true,
      meta: { title: "职位修改" },
      component: () =>
        import(/* webpackChunkName: "job" */ "../views/job/JobAdd.vue"),
    },
    {
      path: "list",
      name: "jobList",
      meta: { title: "职位列表" },
      component: () =>
        import(/* webpackChunkName: "job" */ "../views/job/JobList.vue"),
    },
  ],
};
