let dataDictMap = {};

export const cacheDictData = (data) => {
  for (const item of data) {
    if (!dataDictMap[item.dataDictKey]) {
      dataDictMap[item.dataDictKey] = [];
    }
    dataDictMap[item.dataDictKey].push(item);
  }
  localStorage.setItem("dict", JSON.stringify(data));
};

export const getDictData = () => {
  const str = localStorage.getItem("dict");
  if (str) {
    return JSON.parse(str);
  }
  return {};
};

export const getDictDataByKey = (key) => {
  return dataDictMap[key];
};

export const getDictDataNameByKeyValue = (key, value) => {
  const arr = getDictDataByKey(key);
  for (const item of arr) {
    if (item.dataDictValue === value) {
      return item.dataDictLabel;
    }
  }
  return value;
};
