import { BlankLayout } from "@/layouts";

export default {
  path: "/employee",
  name: "employee",
  component: BlankLayout,
  props: true,
  redirect: "/employee/add",
  children: [
    {
      path: "add",
      name: "employeeAdd",
      meta: { title: "员工新增" },
      component: () =>
        import(/* webpackChunkName: "employee" */ "../views/employee/EmployeeAdd.vue"),
    },
    {
      path: "update/:code",
      name: "employeeUpdate",
      props: true,
      meta: { title: "员工修改" },
      component: () =>
        import(/* webpackChunkName: "employee" */ "../views/employee/EmployeeAdd.vue"),
    },
    {
      path: "list",
      name: "employeeList",
      meta: { title: "员工列表" },
      component: () =>
        import(/* webpackChunkName: "employee" */ "../views/employee/EmployeeList.vue"),
    },
    {
      path: "modifyPwd",
      name: "employeeModifyPwd",
      meta: { title: "员工列表" },
      component: () =>
        import(/* webpackChunkName: "employee" */ "../views/employee/EmployeeModify.vue"),
    },
    {
      path: "my",
      name: "employeeMy",
      props: true,
      meta: { title: "我的信息" },
      component: () =>
        import(/* webpackChunkName: "employee" */ "../views/employee/EmployeeAdd.vue"),
    },
  ],
};
