import { BlankLayout } from "@/layouts";

export default {
  path: "/salary",
  name: "salary",
  component: BlankLayout,
  props: true,
  redirect: "/salary/add",
  children: [
    {
      path: "add",
      name: "salaryAdd",
      meta: { title: "薪酬新增" },
      component: () =>
        import(/* webpackChunkName: "salary" */ "../views/salary/SalaryAdd.vue"),
    },
    {
      path: "list",
      name: "salaryList",
      meta: { title: "薪酬列表" },
      component: () =>
        import(/* webpackChunkName: "salary" */ "../views/salary/SalaryList.vue"),
    },
    {
      path: "my",
      name: "salaryMy",
      props: true,
      meta: { title: "我的薪酬" },
      component: () =>
        import(/* webpackChunkName: "salary" */ "../views/salary/SalaryList.vue"),
    },
  ],
};
