<template>
  <a-layout
    id="components-layout-demo-custom-trigger"
    :style="{ height: `${appHeight}px` }"
  >
    <a-layout-sider :trigger="null" style="height: 100%; overflow: auto">
      <div class="logo">HRMS</div>
      <Menu :menus="menus" :openKeys="openKeys"></Menu>
    </a-layout-sider>
    <a-layout style="height: 100%">
      <a-layout-header
        class="header"
        style="background: #fff; padding: 0 20px 0 0"
      >
        <a-popover>
          <template #content>
            <a-button @click="handeExit()">退出登录</a-button>
            <a-button @click="handleUpdate()">修改密码</a-button>
          </template>
          <div style="display: flex; align-items: center">
            <a-badge :count="approveNum" style="margin-right: 10px">
              <BellOutlined style="font-size: 25px; margin-right: 14px" @click="handleLeaveApprove"/>
            </a-badge>

            <a-avatar :size="40">
              <template #icon><UserOutlined /></template>
            </a-avatar>
            <span
              style="
                margin: 0 5px 0 10px;
                display: flex;
                flex-direction: column;
              "
            >
              <div style="height: 20px; line-height: 20px">{{ empName }}</div>
              <div style="height: 20px; line-height: 20px">
                [&nbsp;{{ jobName }}&nbsp;]
              </div>
            </span>
          </div>
        </a-popover>
      </a-layout-header>
      <a-layout-content
        :style="{
          margin: '24px 16px',
          padding: '24px',
          background: '#fff',
          minHeight: '280px',
        }"
      >
        <router-view />
      </a-layout-content>
    </a-layout>
  </a-layout>
</template>
<script>
import { ref } from "vue";
import { UserOutlined, BellOutlined } from "@ant-design/icons-vue";
import { defineComponent } from "vue";
import Menu from "./Menu";
import router from "@/router";
import { cleanUserInfo, getUserInfo } from "@/utils/authUtil";
import { JOB, JOB_NAME } from "@/utils/constant";

import leaveService from "@/service/leaveService";

const originMenus = [
  { id: "welcome", label: "欢迎页", url: "/welcome" },
  {
    id: "contract-1",
    label: "合同管理",
    children: [
      {
        id: "contract-1-1",
        label: "合同新增",
        url: "/contract/add",
        roles: [JOB.HRMgr],
      },
      {
        id: "contract-1-2",
        label: "合同列表",
        url: "/contract/list",
        roles: [JOB.HRMgr],
      },
      { id: "contract-1-3", label: "我的合同", url: "/contract/my" },
    ],
  },
  {
    id: "dept-1",
    label: "部门管理",
    children: [
      {
        id: "dept-1-1",
        label: "部门新增",
        url: "/dept/add",
        roles: [JOB.SysMgr],
      },
      {
        id: "dept-1-2",
        label: "部门列表",
        url: "/dept/list",
        roles: [JOB.SysMgr],
      },
    ],
  },
  {
    id: "job-1",
    label: "职位管理",
    children: [
      {
        id: "job-1-1",
        label: "职位新增",
        url: "/job/add",
        roles: [JOB.SysMgr],
      },
      {
        id: "job-1-2",
        label: "职位列表",
        url: "/job/list",
        roles: [JOB.SysMgr],
      },
    ],
  },
  {
    id: "emp-1",
    label: "员工管理",
    children: [
      {
        id: "employee-1-1",
        label: "员工新增",
        url: "/employee/add",
        roles: [JOB.HRMgr],
      },
      {
        id: "employee-1-2",
        label: "员工列表",
        url: "/employee/list",
        roles: [JOB.HRMgr],
      },
      { id: "employee-1-3", label: "我的信息", url: "/employee/my" },
    ],
  },
  {
    id: "leave-1",
    label: "请假管理",
    children: [
      { id: "leave-1-1", label: "请假新增", url: "/leave/add" },
      {
        id: "leave-1-2",
        label: "请假列表",
        url: "/leave/list",
        roles: [JOB.HRMgr],
      },
      { id: "leave-1-3", label: "我的请假", url: "/leave/my" },
    ],
  },
  {
    id: "salary-1",
    label: "薪资管理",
    children: [
      { id: "salary-1-1", label: "生成工资单", url: "/salary/add" },
      {
        id: "salary-1-2",
        label: "工资单列表",
        url: "/salary/list",
        roles: [JOB.HRMgr],
      },
      { id: "salary-1-3", label: "我的工资单", url: "/salary/my" },
    ],
  },
  {
    id: "dataDictionary-1",
    label: "数据字典管理",
    children: [
      {
        id: "dataDictionary-1-1",
        label: "数据字典新增",
        url: "/dataDictionary/add",
        roles: [JOB.SysMgr],
      },
      {
        id: "dataDictionary-1-2",
        label: "数据字典列表",
        url: "/dataDictionary/list",
        roles: [JOB.SysMgr],
      },
    ],
  },
];

export default defineComponent({
  components: {
    Menu,
    UserOutlined,
    BellOutlined,
  },
  setup() {
    return {
      empName: getUserInfo().empName,
      jobName: JOB_NAME[getUserInfo().jobCode],
      menus: ref([]),
      appHeight: window.innerHeight,
      openKeys: [
        "contract-1",
        "dept-1",
        "leave-1",
        "job-1",
        "emp-1",
        "dataDictionary-1",
        "salary-1"
      ],
      approveNum: ref(0),
    };
  },
  mounted() {
    const { jobCode, deptCode } = getUserInfo();
    if (jobCode === JOB.SysMgr) {
      this.menus = originMenus;
    } else {
      const menus = [];
      this.filtersMenus(originMenus, menus, jobCode);
      this.menus = menus;
    }

    if (jobCode === "Mgr" || jobCode === "HRMgr") {
      leaveService.queryDeptUnapprovalLeaveNum({ deptCode }).then((res) => {
        this.approveNum = res;
      });
    }
  },
  methods: {
    handeExit() {
      cleanUserInfo();
      router.push({ path: "/login" });
    },
    handleUpdate() {
      router.push({ path: "/employee/modifyPwd" });
    },
    handleLeaveApprove(){
      router.push({ path: "/leave/approve" });
    },
    filtersMenus(originMenus, menus, jobCode) {
      for (const menuItem of originMenus) {
        if (menuItem.children && menuItem.children.length > 0) {
          const copyItem = { ...menuItem, children: [] };
          menus.push(copyItem);
          this.filtersMenus(menuItem.children, copyItem.children, jobCode);
        } else {
          if (menuItem.roles && menuItem.roles.length > 0) {
            // 需要过滤
            if (menuItem.roles.indexOf(jobCode) >= 0) {
              // 有权限
              menus.push({ ...menuItem });
            }
          } else {
            // 不需要过滤
            menus.push({ ...menuItem });
          }
        }
      }
    },
  },
});
</script>
