import { message, notification } from "ant-design-vue";
import qs from "qs";

/**
 * 请求异常
 */
export class RequestError extends Error {
  constructor(text, request) {
    super(text);
    this.name = "RequestError";
    this.request = request;
  }
}

export function isURLSearchParams(val) {
  return (
    typeof URLSearchParams !== "undefined" && val instanceof URLSearchParams
  );
}

export function getParamObject(val) {
  if (isURLSearchParams(val)) {
    return qs.parse(val.toString());
  }
  if (typeof val === "string") {
    return [val];
  }
  return val;
}

export function validateStatus(status) {
  switch (status) {
    case 400:
      message.error("request error");
      break;
    case 404:
      message.warning("Not Found");
      break;
    case 500:
      message.warning("service error");
      break;
    default:
      break;
  }
  return status >= 200 && status < 300;
}

export function errorHandler(error) {
  const { data, status, statusText } = error.response;
  notification.error({
    message: "请求异常！请联系管理员！",
    description: `${status}--${statusText}`,
  });
  return { data, status, statusText };
}

export function requestMiddleware(config) {
  const { data, headers, contentType } = config;
  const reqHead = {
    ...headers,
    "Content-Type":
      contentType || "application/x-www-form-urlencoded;charset=utf-8",
  };

  console.log(contentType ? JSON.stringify(data) : qs.stringify(data))
  return {
    ...config,
    headers: reqHead,
    data: contentType ? JSON.stringify(data) : qs.stringify(data),
  };
}

export function responseMiddleware(response) {
  const { status, statusText } = response;
  const { code, message, data } = response.data;

  if (code === 200) {
    return {
      data,
      status,
      statusText,
    };
  } else {
    notification.error({
      message: code,
      description: message,
    });
    return response.data;
  }
}
