import { BlankLayout } from "@/layouts";

export default {
  path: "/contract",
  name: "contract",
  component: BlankLayout,
  props: true,
  redirect: "/contract/add",
  children: [
    {
      path: "add",
      name: "contractAdd",
      meta: { title: "合同新增" },
      component: () =>
        import(/* webpackChunkName: "contract" */ "../views/contract/ContractAdd.vue"),
    },
    {
      path: "update/:code",
      name: "contractUpdate",
      props: true,
      meta: { title: "合同修改" },
      component: () =>
        import(/* webpackChunkName: "contract" */ "../views/contract/ContractAdd.vue"),
    },
    {
      path: "list",
      name: "contractList",
      meta: { title: "合同列表" },
      component: () =>
        import(/* webpackChunkName: "contract" */ "../views/contract/ContractList.vue"),
    },
    {
      path: "my",
      name: "contractMy",
      props: true,
      meta: { title: "我的合同" },
      component: () =>
        import(/* webpackChunkName: "contract" */ "../views/contract/ContractAdd.vue"),
    },
  ],
};
