import { BlankLayout } from "@/layouts";

export default {
  path: "/dept",
  name: "dept",
  component: BlankLayout,
  props: true,
  redirect: "/dept/add",
  children: [
    {
      path: "add",
      name: "deptAdd",
      meta: { title: "部门新增" },
      component: () =>
        import(/* webpackChunkName: "dept" */ "../views/dept/DeptAdd.vue"),
    },
    {
      path: "update/:code",
      name: "deptUpdate",
      props: true,
      meta: { title: "部门修改" },
      component: () =>
        import(/* webpackChunkName: "dept" */ "../views/dept/DeptAdd.vue"),
    },
    {
      path: "list",
      name: "deptList",
      meta: { title: "部门列表" },
      component: () =>
        import(/* webpackChunkName: "dept" */ "../views/dept/DeptList.vue"),
    },
  ],
};
