export default {
  name: "SliderMenu",
  props: {
    menus: Array,
    openKeys: [],
  },
  methods: {
    renderMenuItem(menu) {
      if (menu.children && menu.children.length > 0) {
        return (
          <a-sub-menu key={menu.id} title={menu.label}>
            {menu.children.map((item) => {
              return this.renderMenuItem(item);
            })}
          </a-sub-menu>
        );
      } else if (menu.url) {
        return (
          <a-menu-item key={menu.url}>
            <router-link to={menu.url}>{menu.label}</router-link>
          </a-menu-item>
        );
      } else {
        return null;
      }
    },
    renderMenu() {
      if (!this.menus || this.menus.length < 0) {
        return;
      }
      return this.menus.map((item) => {
        return this.renderMenuItem(item);
      });
    },
  },
  render() {
    return (
      <a-menu mode="inline" theme="dark" openKeys={this.openKeys} >
        {this.renderMenu()}
      </a-menu>
    );
  },
};
